const code = `let component = ReasonReact.statelessComponent("Button");
module Styles = {
  open Css;
  let button =
    style([
      display(\`flex),
      justifyContent(center),
      alignItems(center),
      minWidth(px(50)),
      minHeight(px(50)),
      backgroundColor(hex("FC6E55")),
      color(white),
      borderRadius(\`percent(50.0)),
      hover([backgroundColor(hex("af4635")), cursor(\`pointer)]),
    ]);
};
let make = (~onClick, children) => {
  ...component,
  render: _self =>
    <div onClick className=Styles.button>
      {ReasonReact.array(children)}
    </div>,
};`

export default code
