const code = `/* Raw Javascript */
[%raw {|
    console.log("Hello!")
|}]

/* Import global */
type timerId;
[@bs.val] external setTimeout: (unit => unit, int) => timerId = "setTimeout";
[@bs.val] external clearTimeout: timerId => unit = "clearTimeout";

/* Import default */
[@bs.module] external leftPad: string => int => string = "./leftPad";

/* Import module field */
[@bs.module "fs"] external readFileSync: string => string = "readFileSync";`

export default code
