const code = `type language = Italian | English | French | Other;
...
let greeting = user => {
  let greetName =
    switch (user.name) {
    | Some(name) => name
    | None => "@" ++ user.username
    };
  switch (user.lang) {
  | Italian => "Ciao, " ++ greetName
  | English
  | Other => "Hi, " ++ greetName
  };
};
`

export default code
