const code = `type state = {count: int};
type action = Increment | Decrement;
let component = ReasonReact.reducerComponent("Counter");
let make = _children => {
  ...component,
  initialState: () => {count: 0},
  reducer: (action, state) =>
    switch (action) {
    | Increment => ReasonReact.Update({count: state.count + 1})
    | Decrement => ReasonReact.Update({count: state.count - 1})
    },
  render: self =>
    <div>
      <button onClick={_event => self.send(Decrement)}>
        {ReasonReact.string("-")}
      </button>
      {self.state.count |> string_of_int |> ReasonReact.string}
      <button onClick={_event => self.send(Increment)}>
        {ReasonReact.string("+")}
      </button>
    </div>,
};
`

export default code
