const code = `let greeting = (~user) => {
  let greetName =
    switch (user.name) {
    | Some(name) => name
    | None => "@" ++ user.username
    };
  switch (user.lang) {
  | Italian => "Ciao, " ++ greetName
  | English
  | Other => "Hi, " ++ greetName
  };
};
greeting(~user={
  username: "illbexyz",
  name: None,
  lang: Italian
}); /* Ciao, @illbexyz */

`

export default code
