import registerServiceWorker from "./registerServiceWorker"

require("./index.css")

ReactDOM.render(
  <div>
    <Presentation />
    <footer>
      <div style={{ position: "fixed", bottom: 8, left: 16 }}>
        <a className={"footerLink"} href="https://twitter.com/illbexyz">
          @illbexyz
        </a>
      </div>
      <div style={{ position: "fixed", bottom: 8, right: 16 }}>
        <a className={"footerLink"} href="https://illbe.xyz/talks/reasonml">
          illbe.xyz/talks/reasonml
        </a>
      </div>
    </footer>
  </div>,
  document.getElementById("root")
)
registerServiceWorker()

window.addEventListener("keydown", event => {
  if (event.key === "ArrowUp") {
    simulateKey(37)
  } else if (event.key === "ArrowDown") {
    simulateKey(39)
  }
})

/**
 * Simulate a key event.
 * @param {Number} keyCode The keyCode of the key to simulate
 * @param {String} type (optional) The type of event : down, up or press. The default is down
 * @param {Object} modifiers (optional) An object which contains modifiers keys { ctrlKey: true, altKey: false, ...}
 */
function simulateKey(keyCode, type, modifiers) {
  var evtName = typeof type === "string" ? "key" + type : "keydown"
  var modifier = typeof modifiers === "object" ? modifier : {}

  var event = document.createEvent("HTMLEvents")
  event.initEvent(evtName, true, false)
  event.keyCode = keyCode

  for (var i in modifiers) {
    event[i] = modifiers[i]
  }

  document.dispatchEvent(event)
}
